  --main: #064e3b;
  --accent: #10b981;
  --accent2: #6ee7b7;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  width: 100%;
}

.app {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 225px 1fr;
}

header {
  grid-row: 1 / 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
}

/* Desktop Navigation */

.nav-item,
.nav-item li {
  list-style-type: none;
}

.home-icon {
  position: relative;
  display: inline-block;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  width: 18px;
  height: 18px;
  background-size: cover;
}

.desktop-nav {
  background-color: var(--main);
  width: 65%;
  border-radius: 50px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
  font-size: 20px;
}

@media (max-width: 992px) {
  .desktop-nav {
    display: none;
  }
}

.nav-item {
  position: relative;
  display: inline-block;
  margin-right: 35px;
  padding: 18px;
}

.desktop-nav me .nav-item:hover {
  cursor: pointer;
  background-color: var(--accent);
}

.desktop-nav .nav-item:last-child {
  margin-right: 0;
}

.dropdown-content::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 20px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid var(--accent);
}

.dropdown-content li {
  padding: 5px;
}

.dropdown-content li:hover {
  color: var(--main);
}

.shopping-drop .inner-dropdown {
  display: flex;
  justify-content: center;
}

.shopping-drop li {
  margin-right: 10px;
}

.dropdown-content {
  visibility: hidden;
  position: absolute;
  left: -25px;
  top: 100%;
  z-index: 1000;
  border: 25px solid transparent;
}

.inner-dropdown {
  background-color: var(--accent);
  padding: 10px;
  width: 250px;
  border-radius: 8px;
}

.reveal {
  visibility: visible;
}

/* Mobile Navigation */

@media (min-width: 992px) {
  .mobile-nav {
    display: none;
  }
}

.mobile-nav {
  background-color: var(--main);
  width: 100%;
  height: 50px;
  grid-row: 1 / 2;
  position: relative;
}

.menu-icon {
  position: absolute;
  top: 15px;
  left: 50%;
  right: 50%;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  width: 22px;
  height: 22px;
  background-size: cover;
  margin-left: -10px;
}

.mobile-nav .home-icon {
  position: absolute;
  top: 15px;
}

.mobile-nav .nav-item:not(:nth-child(1)):not(:nth-child(2)) {
  position: relative;
  text-align: center;
  top: -10px;
  width: 100%;
  background-color: var(--accent2);
  margin-top: -1px;
  /* display: none; */
}

.hide {
  display: none;
}

/* Slider */

main {
  display: flex;
  justify-content: center;
}

.frame {
  /* border: 1px dashed blue; */
  width: 850px;
  height: 525px;
  display: grid;
  grid-template-columns: 50px 1fr 50px;
  justify-items: center;
  align-items: center;
}

.previous {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  width: 64px;
  height: 64px;
  background-size: cover;
  border: none;
  background-color: transparent;
}

.previous:hover {
  transform: scale(1.07);
}

.next {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  width: 64px;
  height: 64px;
  background-size: cover;
  border: none;
  background-color: transparent;
}

.next:hover {
  transform: scale(1.07);
}

.slider {
  /* border: 1px dashed red; */
  width: 640px;
  height: 427px;
  grid-column: 2 / 3;
}

.slider-img {
  width: 640px;
  height: 427px;
  border-radius: 8px;
  box-shadow: 5px 5px 5px 2px rgb(0, 0, 0, 0.33);
}

.slide-1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.slide-2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.slide-3 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.slide-4 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

.slide-5 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

.dot-container {
  /* border: 1px dashed green; */
  width: 720px;
  height: 30px;
  grid-column: 2 / 3;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.dots {
  background-color: rgb(0, 0, 0, 0.09);
  width: 18px;
  height: 18px;
  border-radius: 100px;
}

.dots:hover {
  cursor: pointer;
}

.dot-active {
  background-color: var(--main);
}
